import React, { Component } from 'react';

import '../animations.css';
import '../NewApp.css';

import { Tooltip, Collapse, Row, Col, Button, Icon  } from 'antd';

const Panel = Collapse.Panel;

class Item extends Component {
    render() { 
        const indexItem = this.props.indexItem;
        const numChildCategory = this.props.numChildCategory;
        const currentTitre = this.props.currentTitre;
        const data = this.props.data;
        const arrayData = [];
        if(!data) {
            return null
        }
        Object.keys(data).map(item => item === currentTitre ? Object.keys(data[item]).map(category => Object.keys(data[item][category]).map(categoryItem => arrayData.push(data[item][category][categoryItem]))): null);

        const liste = (arrayData || []).map((elt, id) =>
            <Collapse 
                defaultActiveKey={elt.titreCat} 
                key={`titreCat-${id}`}
                id={`titreCat-${id}`}
            >
                <Panel header={
                    <Row type="flex" justify="space-around" align='middle'>
                        <Col xs={18} md={20}  lg={22}>
                            {elt.titreCat}
                        </Col>
                        <Col xs={3} md={2} lg={1}>
                            <input type='checkbox' id={elt.titreCat}/>
                        </Col>
                        <Col xs={3} md={2} lg={1}>
                        <button 
                        name="trash" 
                        className="trash" 
                        onClick={() => this.props.deleteCategory(elt, id)}>
                            <Icon type='delete' style={{ color : '#FFF', fontSize: '1.4em' }}/>
                        </button>
                        </Col>
                    </Row>
                    } 
                    key={elt.titreCat} 
                    id={elt.titreCat} 
                    accordion={true}
                    >   
                    {   
                        (Object.keys(elt.categoryItems || [])).map((item, id) =>
                        <Row style={{ lineHeight: '1em' }} type="flex" justify="space-between" align="middle" className={indexItem === `elt-${elt.categoryItems[item].item}` && numChildCategory > 1 ? 'item-container message-leave': 'item-container'} id={`elt-${elt.categoryItems[item].key}`} key={elt.categoryItems[item].key}>
                            <Col xs={4} sm={3} md={2} lg={1}>
                                <Tooltip placement="topLeft" title='Valider' mouseEnterDelay={0.5} >
                                    <Button
                                        className={elt.categoryItems[item].checked ? 'isChecked' : 'checked'}
                                        shape="circle"
                                        type='primary'
                                        size='large'
                                        block
                                        onClick={() => this.props.isChecked(elt.categoryItems[item], id)}
                                    >
                                        <Icon type='check'/>
                                    </Button>
                                </Tooltip>
                            </Col>
                            <Col xs={20} sm={21} md={22} lg={23}>
                                <Row 
                                    type="flex" 
                                    justify="space-between" 
                                    align="middle"
                                    className={elt.categoryItems[item].checked ? 'ant-row-flex ant-row-flex-space-between ant-row-flex-middle flip' : 'ant-row-flex ant-row-flex-space-between ant-row-flex-middle'} 
                                    key={elt.categoryItems[item].key}
                                >
                                    <Col xs={12} sm={16} md={18} lg={20} style={{ paddingLeft: '1em' }}>
                                        {elt.categoryItems[item].item}
                                    </Col>
                                    <Col xs={6} sm={4} md={3} lg={2}>
                                        <Tooltip placement="top" title='Supprimer' mouseEnterDelay={0.5}>
                                            <Button 
                                                style={{ background: 'transparent' }}
                                                name="trash" 
                                                className="trash"
                                                type='primary'
                                                // size='large'
                                                block
                                                onClick={() => this.props.delete(elt.categoryItems[item], id)}>
                                                    <Icon type='delete' style={{ color : '#009688', fontSize: '1.4em' }}/>
                                            </Button>
                                        </Tooltip>
                                    </Col>
                                    <Col xs={6} sm={4} md={3} lg={2}>
                                        <Tooltip placement="top" title='Commentaire' mouseEnterDelay={0.5}>
                                            <Button 
                                                name="comment" 
                                                className='comment' 
                                                style={{ background: 'transparent', border: 'none' }}
                                                type='primary'
                                                // size='large'
                                                block
                                                onClick={() => this.props.toggleComment(elt.categoryItems[item])}>
                                                {
                                                    elt.categoryItems[item].comment === '' ?
                                                    <Icon type='message' style={{color: '#009688', fontSize: '1.4em' }}/>
                                                    :
                                                    <Icon type='message' style={{color: '#FFF', background: '#009688', fontSize: '1.4em' }}/>
                                                }
                                            </Button>
                                        </Tooltip>
                                    </Col>
                                </Row>
                            </Col>
                                <Col span={23} push={1}>
                                    <form className='form-comm' onSubmit={e => this.props.closeComment(e, elt.categoryItems[item])}>
                                        <input 
                                            value={elt.categoryItems[item].comment} 
                                            name='comment' 
                                            type='text'
                                            id='inputComment'
                                            placeholder='ajouter un commentaire'
                                            onChange={(e) => this.props.addComment(e, elt.categoryItems[item])}
                                            className={elt.categoryItems[item].show ? 'item message-enter-active' : 'item message-leave-active'}
                                            maxLength={50}
                                        />
                                    </form>
                                </Col>
                        </Row>
                            )
                    }
                    </Panel>
                </Collapse>
        )

        return ( 
            <>
                {liste}
            </>
         );
    }
}
 
export default Item;