import React, { Component } from 'react';

import MyLists from './MyLists';
import { Button, Icon, Col } from 'antd';

class Home extends Component {
    render() {
        const indexItem = this.props.indexItem;
        const listeDeBase = this.props.listeDeBase;
        const liste = listeDeBase.map((item, id) => 
        <>  
            <Col xs={{ span: 22 }} md={{ span: 23 }} lg={{ span: 23 }} key={id}>
                <Button 
                    size='large' 
                    block 
                    type="primary" 
                    key={`elt-${item}`} 
                    id={item}
                    onClick={e => this.props.choixListe(e)}
                    className={indexItem === `elt-${item}` ? 'item-titre message-leave': 'item-titre'}
                    >
                        {item}
                </Button>
            </Col>
            <Col xs={{ span: 2 }} md={{ span: 1 }} lg={{ span: 1 }} key={`delete-${id}`}>
                <Button 
                    size='large' 
                    name="trash" 
                    type="primary" 
                    block 
                    className="trash" 
                    onClick={() => this.props.deleteList(item)}
                >  
                    <Icon type='delete' style={{ color : '#009688', fontSize: '1.4em' }}/>
                </Button>
            </Col>
        </>
        );
        return (
            <>
            <Col xs={{ span: 12, push: 6 }} sm={{ span: 10, push: 6 }} md={{ span: 8, push: 8 }} lg={{ span: 6, push: 9}}>
                <h2>Mes Listes</h2>
            </Col>
            {
            !this.props.clicked ?
            <>
                <Col xs={{ span: 24 }} md={{ span: 22, push: 1 }} lg={{ span: 20, push: 2 }}>
                    { 
                        listeDeBase.length >= 3 ?
                        null
                        :
                        <form className='form form-titre' onSubmit={e => this.props.addNewListTitre(e)}>
                            <input type='text' id='form-input' placeholder='Ajouter un titre' maxLength={30}/>
                            <button type='submit' id='myButtonSubmit'>+</button>
                        </form>
                    }
                </Col>
                    <>
                        {liste}
                    </>
                    <Col span={6} style={{ marginRight: '1em' }}>
                        <Button block type="primary" onClick={this.props.returnToApp} className='goBack'>
                            <Icon type="left" />Retour
                        </Button>
                    </Col>
            </>
            :
            <Col span={24}>
            <MyLists
                currentTitre={this.props.currentTitre}
                createNewItem={this.props.createNewItem}
                data={this.props.data}
                isChecked={this.props.isChecked}
                delete={this.props.delete}
                toggleComment={this.props.toggleComment}
                addComment={this.props.addComment}
                titreListe={this.props.titreListe}
                indexInput={this.props.indexInput}
                placeholderElt={this.props.placeholderElt}
                returnToApp={this.props.returnToApp}
                closeComment={this.props.closeComment}
                goHome={this.props.goHome}
                indexItem={this.props.indexItem}
                createNewCategory={this.props.createNewCategory}
                isCategory={this.props.isCategory}
                toggleSwitch={this.props.toggleSwitch}
                numChildCategory={this.props.numChildCategory}
                deleteCategory={this.props.deleteCategory}
            />
            </Col>
            }
            </>
        )
    }
}
 
export default Home;